/**
 * 流程设计器事件列表
 */
export enum PmfEvents {
    /**
     * 重置画布视图
     */
    ResetGraphView = "ResetGraphView",
    /**
     * 画布视图大小改变
     */
    ResizeGraphView = "ResizeGraphView",
    /**
     * 选中元素改变
     */
    SelectionModelChanged = "SelectionModelChanged",
    /**
     * 锁定状态改变
     */
    LockedChanged = "LockedChanged",
    /**
     * 按钮状态改变
     */
    StateChanged = "StateChanged",
    /**
     * 网格启用状态改变
     */
    GridEnabledChanged = "GridEnabledChanged",
    /**
     * 节点从侧边栏拖入画布事件
     */
    CellsInserted = "CellsInserted",
    /**
     * 显示属性事件
     */
    ShowProperty = "ShowProperty",
    /**
     * 名称属性修改事件
     */
    NameChanged = "NameChanged"
}